<template>
    <div class="topnav">
      <div class="heade">
        <p >{{p}}</p>
        <div class="first_div" @click="input">
          <span></span>
          <b>搜索</b>
        </div>
        <!-- 这里用到了调用子组件的方法/此为子组件 -->
        <div style="margin-top:0.3rem;" @click="clickme">
          <img style="width:2rem;" v-lazy="require('../../../assets/icon/'+src+'.png')" alt="">
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      src:'yuyin',
    }
  },
  props:["p"],
  methods:{
    input(){
      this.$router.push("search");
    },
    clickme(){
      if(this.p=="我的"){
        this.$router.push("register");
      }
    }
  },
  created(){
    if(this.p=="我的"){
      this.src="guanli2";
    }
  }
};
</script>

<style scoped>
.topnav{padding:1rem;}
.heade {
  display: flex;
  align-items: center;
}
.heade > p {
  margin-top: 0.5rem;  
  width: 25%;
  font-size: 1.5rem;
  color:#000;
}
.first_div{
  display: flex;
  align-items: center;
  justify-content: center;
  height:2rem;
  width: 60%;
  line-height: 2rem;
  color:#aaa;
  font-size:0.9rem;
  background: #fff;
  border-radius: 3rem;
  margin-right:1rem;
}
.first_div>span{
  margin-top:0.3rem;
  display: inline-block;
  width: 1.5rem;height:1.4rem;
  background:url(../../../assets/icon/icon.png) no-repeat -2% 12%;
}
img[lazy=loading] {
  width: 100%;
  height: 200px;
  margin: auto;
  background: url("../../../assets/register.gif") no-repeat fixed center;
}
</style>
